.TimeBox {
    background: #D8D8D8;
    color: #4A4A4A;
    height: 204px;
    width: 216px;
    -webkit-box-shadow: 1px 1px 2px 0px rgba(0,0,0,0.5);
            box-shadow: 1px 1px 2px 0px rgba(0,0,0,0.5);
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 24px;
}

.TimeBox_highlight {
    outline: #008C7D solid 2px;
}

.TimeBox:not(:last-child) {
    margin-right: 24px;
}

.TimeBox-daylight {
    width: 24px;
    height: 100%;
}

.TimeBox-content {
