}

.TimeBox-datetime .TimeBox-time input[type="text"] {
    font-size: 3em;
    font-weight: 300;
    font-family: 'Roboto', sans-serif;
    background-color: transparent;
    margin: 0;
    border: 0;
    width: 100%;
