}

.TimeBox-timezone {
    font-weight: 700;
    text-align: right;
}

.TimeBox-timezone-offset {
    font-weight: 100;
}

.TimeBox-close svg {
    height: 14px;
    width: 14px;
}

.TimeBox-close:hover {
    fill: #008C7D;
}


